@charset "UTF-8";

/* RESET CSS */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
samp,
small,
strong,
sub,
sup,
var,
b,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
figcaption,
figure,
hgroup,
menu,
nav,
section,
summary,
time,
mark,
audio,
video {
    margin: 0;
    padding: 0;
    border: 0;
    background: transparent;
    -webkit-font-smoothing: antialiased;
    letter-spacing: -0.25px;
}
html,
body {
    width: 100%;
    height: 100%;
}
body {
    line-height: 1;
    font-family: "Noto Sans KR", sans-serif;
    white-space: normal;
    word-wrap: break-word;
    word-break: keep-all;
    -webkit-text-size-adjust: none;
    color: #333;
    font-size: 14px;
    font-weight: 400;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
    display: block;
}
img,
fieldset {
    border: 0 none;
    margin: 0;
}
ul,
ol,
li {
    list-style: none;
}
hr {
    display: none;
}
legend,
caption {
    display: none;
    clear: both;
}
table {
    border-collapse: collapse;
    border: 0;
}
textarea,
input,
button {
    border-radius: 0;
}
em {
    font-style: normal;
}
textarea {
    resize: none;
}
label {
    cursor: pointer;
}
label,
textarea,
input,
select,
button {
    vertical-align: middle;
}
pre {
    overflow-x: scroll;
}
button {
    overflow: visible;
    cursor: pointer;
    background: none;
    border: 0;
}
button[disabled="disabled"] {
    cursor: default;
}
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
    margin: 0;
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
}
input::-ms-clear,
input::-ms-reveal {
    display: none;
    width: 0;
    height: 0;
}
input::-webkit-search-decoration,
input::-webkit-search-cancel-button,
input::-webkit-search-results-button,
input::-webkit-search-results-decoration {
    display: none;
}
input {
    border: 0;
    background: #fff;
    width: inherit;
    vertical-align: middle;
}
input:placeholder {
    color: #aaa;
}
input {
    font-family: inherit;
    color: inherit;
    font-weight: 500;
}
blockquote,
q {
    quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
    content: "";
    content: none;
}
a {
    margin: 0;
    padding: 0;
    text-decoration: none;
    background: transparent;
    color: inherit;
    display: inline-block;
}
a,
a:link {
    text-decoration: none;
}
a,
button,
select,
input {
    font-family: inherit;
}
i {
    font-style: normal;
}
input::-webkit-input-placeholder {
    color: #aaa;
}
input:-ms-input-placeholder {
    color: #aaa;
}
textarea::-webkit-input-placeholder {
    color: #aaa;
}
textarea:-ms-input-placeholder {
    color: #aaa !important;
}
input:focus,
button:focus,
select:focus,
textarea:focus,
button:focus,
a:focus {
    outline: none;
}
select,
input,
textarea {
    line-height: 1.5;
    font-family: "Noto Sans KR", sans-serif;
    font-weight: 500;
    color: #666;
}
select::-ms-expand {
    display: none;
}
button span {
    pointer-events: none;
}
